import { Fragment, useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { useTranslation } from "react-i18next"
import "./style.scss"
import { selectThemeColors } from "@utils"
import classnames from "classnames"
import Select from "react-select"

import { storeName, getDataList, getAlwaysGrid } from "./store"

const List = ({
  lookupMode_fieldName = "",
  lookupMode_isMulti = false,
  lookupMode_onChange,
  lookupMode_defaultValue
}) => {
  const { t } = useTranslation()
  const dispatch = useDispatch()
  const store = useSelector((state) => state[storeName])

  const onChangeInput = (e) => {
    return lookupMode_onChange(e, lookupMode_fieldName)
  }

  useEffect(() => {
    if (store.data.length === 0 || (!store.isLoading && getAlwaysGrid)) {
      dispatch(getDataList({}))
    }
  }, [dispatch, store.data.length])

  return (
    <Fragment>
      <Select
        theme={selectThemeColors}
        defaultValue={
          !lookupMode_isMulti
            ? store.dataDropdown.filter(
                (el) => el.value === lookupMode_defaultValue
              )
            : lookupMode_defaultValue
        }
        isMulti={lookupMode_isMulti}
        // onChange={lookupMode_onChange}
        onChange={onChangeInput}
        options={store.dataDropdown}
        className={classnames("react-select", {
          "is-invalid": store.selectedData?.id === null
        })}
        classNamePrefix="select"
      />
    </Fragment>
  )
}

export default List
